@import 'src/assets/styles/mixins';

.contactItem {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  .userPhoto {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    margin-right: 5px;
    display: inline-block;
  }

  .userName {
    @include text($weight: 600);
  }
}