@import "src/assets/styles/mixins";
@import "src/assets/styles/variables";

.superLink {
  a {
    text-decoration: none;
    @include text($weight: 600);
  }
}
.active {
  a{
    color: $primaryColor!important;
    &:before{
      visibility: visible!important;
      height: 100%!important;
      opacity: 1!important;
    }
  }
}